<template>
	<div>
		<el-form :inline="true" :model="query" class="query-form">
			<el-form-item class="query-form-item">
				<el-input v-model="query.keyword" class="width200" placeholder=""></el-input>
			</el-form-item>
			<el-form-item class="query-form-item">
				<el-date-picker
					v-model="query.date"
					value-format="timestamp"
					type="datetime"
					placeholder=""
				></el-date-picker>
			</el-form-item>
			<el-form-item>
				<el-button-group>
					<el-button type="primary" icon="search" @click="onSubmit"></el-button>
					<el-button type="primary" @click.native="handleForm(null, null)"></el-button>
				</el-button-group>
			</el-form-item>
		</el-form>
		<el-table
			class="common-table"
			v-loading="loading"
			:data="list"
			style="width: 100%;"
			max-height="500px"
		>
			<el-table-column label="" align="center" prop="name" width="100px"></el-table-column>
			<el-table-column label="" width="100px" align="center">
				<template slot-scope="scope">
					<span>{{scope.row.temperature?scope.row.temperature+'':''}}</span>
				</template>
			</el-table-column>
			<el-table-column label="" prop="job_type" align="center"></el-table-column>
			<el-table-column label="" width="110px" prop="link_tel" align="center"></el-table-column>
			<el-table-column label="" width="110px" prop="link_man" align="center"></el-table-column>
			<el-table-column label="" prop="id_card" width="170px" align="center"></el-table-column>
			<el-table-column label="" min-width="100px" align="center">
				<template slot-scope="scope">
					<span>{{scope.row.province}}{{scope.row.city}}{{scope.row.area}}{{scope.row.address}}</span>
				</template>
			</el-table-column>
			<el-table-column label="" align="center" width="50px">
				<template slot-scope="scope">
					<span>{{scope.row.sex===1?'':''}}</span>
				</template>
			</el-table-column>
			<el-table-column label="" align="center" min-width="120px" fixed="right">
				<template slot-scope="scope">
					<el-button type="text" size="small" @click.native="handleForm(scope.$index, scope.row)"></el-button>
					<el-button type="text" size="small" @click.native="viewDetail(scope.$index, scope.row)"></el-button>
					<el-button type="text" size="small" @click.native="handleDel(scope.$index, scope.row)"></el-button>
				</template>
			</el-table-column>
		</el-table>
		<el-pagination
			:page-size="query.limit"
			@current-change="handleCurrentChange"
			layout="prev, pager, next,total"
			:total="total"
		></el-pagination>
		<!---->
		<el-dialog
			:title="formMap[formName]"
			:visible.sync="formVisible"
			:before-close="hideForm"
			width="40%"
			top="5vh"
			class="form-dialog"
		>
			<el-form
				:model="formData"
				:inline="true"
				label-width="160px"
				label-position="right"
				class="form"
				:rules="addRules"
				ref="dataForm"
			>
				<el-form-item label="" prop="tel">
					<el-input class="width240" placeholder="" v-model="formData.tel" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="name">
					<el-input v-model="formData.name" placeholder="" class="width240" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="job_type">
					<el-select v-model="formData.job_type" class="width240" placeholder="">
						<el-option
							v-for="(item, index) in options"
							:key="item.id"
							:label="item.name"
							:value="item.id"
						></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="" prop="id_card">
					<el-input v-model="formData.id_card" placeholder="" class="width240" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" placeholder="">
					<el-radio-group class="width240" v-model="formData.sex">
						<el-radio :label="1"></el-radio>
						<el-radio :label="2"></el-radio>
					</el-radio-group>
				</el-form-item>
				<el-form-item label="" prop="link_tel">
					<el-input
						class="width240"
						placeholder=""
						v-model="formData.link_man"
						auto-complete="off"
					></el-input>
				</el-form-item>
				<el-form-item label="" prop="address">
					<div class="width240 select-input">
						<selectCity @change="districtChange" :address="address"></selectCity>
					</div>
				</el-form-item>
				<el-form-item label="" prop="address">
					<el-input class="width240" placeholder="" v-model="formData.address" auto-complete="off"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="hideForm"></el-button>
				<el-button type="primary" @click.native="formSubmit()" :loading="formLoading"></el-button>
			</div>
		</el-dialog>
		<workerDetail
			:workerId="workerId"
			:formVisible="formDetailVisible"
			@hideForm="formDetailVisible=false"
		></workerDetail>
	</div>
</template>

<script>
import {
	getWorkmanList,
	updateWorkman,
	addWorkman,
	deleteWorkman,
	getWorkmanDetail
} from "../../api/workman/index";
import WorkerDetail from "../../components/modal/workerDetail.vue";
import selectCity from "../../components/selectCity.vue";
import { geTypeAll } from "../../api/file/data"
import { validateIdCard, checkMobile } from "../../utils/util.js";
const formJson = {
	id: "",
	sex: "",
	name: "",
	id_card: "",
	tel: '',
	job_type: '',
	link_man: '',
	link_tel: '',
	address: '',
	roles: []
};
export default {
	components: {
		WorkerDetail: WorkerDetail,
		selectCity: selectCity
	},
	data() {
		let validatereg = (rule, value, callback) => {
			//
			let reg = /^1[3456789]\d{9}$/
			if (value === '') {
				this.isShowPhone = true
				callback()
			} else if (!reg.test(value)) {
				this.isShowPhone = false
				callback(new Error(''))
			} else {
				this.isShowPhone = false
				callback()
			}
		}
		var validate = (rule, value, callback) => {
			if (value === '') {
				callback(new Error(''));
			} else {
				if (!validateIdCard(value)) {
					callback(new Error(''));
				}
				callback()
			}
		};
		return {
			formDetailVisible: false,
			workerId: '',
			roles: [],
			query: {
				keyword: '',
				page: 1,
				limit: 20,
				date: ''
			},
			list: [],
			value: '',
			total: 0,
			loading: true,
			index: null,
			formName: null,
			formMap: {
				add: "",
				edit: ""
			},
			options: [],
			formLoading: false,
			formVisible: false,
			formData: formJson,
			address: [],
			formRules: {},
			addRules: {
				name: [
					{ required: true, message: "", trigger: "blur" }
				],
				tel: [
					{ required: true, message: "", trigger: "blur" },
					{ validator: validatereg, trigger: 'blur' }
				],
				job_type: [
					{ required: true, message: "", trigger: "change" }
				],
				id_card: [
					{ required: true, message: "", trigger: "blur" },
					{ validator: validate, trigger: 'blur' }
				],
			},
			deleteLoading: false
		};
	},
	methods: {
		districtChange(val) {
			this.formData.provinceid = val[0]
			this.formData.cityid = val[1]
			this.formData.areaid = val[2]
		},
		onReset() {
			// this.$router.push({
			// 	path: ""
			// });
			this.query = {
				keyword: '',
				page: 1,
				limit: 20,
				date: ''
			};
			this.getList();
		},
		getType(pid) {
			let params = {
				pid: pid,
				keyword: ''
			}
			return new Promise((resolve, reject) => {
				geTypeAll(params).then(res => {
					resolve(res)
				})
			})
		},
		onSubmit() {
			this.$router.push({
				path: "",
				query: this.query
			});
			this.getList();
		},
		handleCurrentChange(val) {
			this.query.page = val;
			this.getList();
		},
		getList() {
			this.loading = true;
			getWorkmanList(this.query)
				.then(response => {
					this.loading = false;
					this.list = response.data || [];
					this.total = response.count || 0;
				})
				.catch(() => {
					this.loading = false;
					this.list = [];
					this.total = 0;
					this.roles = [];
				});
		},
		getRoleList() {
			authAdminRoleList(this.query)
				.then(response => {
					this.roles = response.data.list || [];
				})
				.catch(() => {
					this.roles = [];
				});
		},
		// 
		resetForm() {
			if (this.$refs["dataForm"]) {
				// 
				this.$refs["dataForm"].clearValidate();
				// 
				this.$refs["dataForm"].resetFields();
			}
		},
		// 
		hideForm() {
			// 
			this.formVisible = !this.formVisible;
			// 
			this.$refs["dataForm"].resetFields();
			return true;
		},
		// 
		handleForm(index, row) {
			this.formVisible = true;
			this.formData = JSON.parse(JSON.stringify(formJson));
			if (row !== null) {
				this.formData = Object.assign({}, row);
				if (row.provinceid) {
					console.log(row)
					this.getDetail(row.id)
				}
			}
			this.formName = "add";
			this.formRules = this.addRules;
			if (index !== null) {
				this.index = index;
				this.formName = "edit";
				this.formRules = this.editRules;
			}
		},
		getDetail(id) {
			getWorkmanDetail({ id }).then(res => {
				if (res.provinceid) {
					this.address = [res.provinceid, res.cityid, res.areaid]
				}
				this.formData = res
			})
		},
		viewDetail(index, row) {
			this.workerId = row.id
			this.formDetailVisible = true
		},
		formSubmit() {
			this.$refs["dataForm"].validate(valid => {
				if (valid) {
					this.formLoading = true;
					let data = Object.assign({}, this.formData);
					updateWorkman(data, this.formName).then(response => {
						if (response) {
							this.formLoading = false;
							this.$message.success("");
							this.formVisible = false;
							this.getList()
						} else {
							this.$message.error("");
						}
						this.resetForm();
					});
				}
			});
		},
		// 
		handleDel(index, row) {
			if (row.id) {
				this.$confirm("?", "", {
					type: "warning"
				})
					.then(() => {
						let para = { id: row.id };
						this.deleteLoading = true;
						deleteWorkman(para)
							.then(response => {
								this.deleteLoading = false;
								if (response) {
									this.$message.success("");
									this.getList()
								} else {
									this.$message.error("");
								}
							})
							.catch(() => {
								this.deleteLoading = false;
							});
					})
					.catch(() => {
						this.$message.info("");
					});
			}
		}
	},
	created() {
		// 
		let query = this.$route.query;
		this.query = Object.assign(this.query, query);
		this.query.limit = parseInt(this.query.limit);
		// 
		this.getList();
		this.getType(3).then(res => {
			this.options = res
		})
	}
}

</script>
