<template>
	<el-dialog
		title=""
		:visible.sync="formVisible"
		:before-close="hideForm"
		width="40%"
		top="5vh"
		class="form-detail-dialog detail-view"
	>
		<el-form
			:model="formData"
			:inline="true"
			label-width="160px"
			label-position="right"
			class="form"
			ref="dataForm"
			v-if="formData"
		>
			<el-form-item label="">
				<el-input v-model="formData.name" class="width240" readonly></el-input>
			</el-form-item>
			<el-form-item label="">
				<el-input v-model="formData.job_type_name" class="width240" readonly></el-input>
			</el-form-item>
			<el-form-item label="">
				<el-input v-model="formData.id_card" class="width240" readonly></el-input>
			</el-form-item>
			<el-form-item label="">
				<el-input class="width240" v-model="formData.link_tel" readonly></el-input>
			</el-form-item>
			<el-form-item label="">
				<el-input
					class="width240"
					v-model="formData.link_man"
				readonly
				></el-input>
			</el-form-item>
			<el-form-item label="">
					<el-radio-group class="width240" v-model="formData.sex">
						<el-radio :label="1" disabled></el-radio>
						<el-radio :label="2" disabled></el-radio>
					</el-radio-group>
			</el-form-item>
			<el-form-item label="">
				<el-input
					class="width240"
					v-model="formData.address"
			  	readonly
				></el-input>
			</el-form-item>
			<el-form-item label="">
				<el-input
					class="width240"
					placeholder=""
					v-model="formData.address"
				readonly
				></el-input>
			</el-form-item>
		</el-form>
	</el-dialog>
</template>
<script>
import {
	getWorkmanDetail
} from "../../api/workman/index";
export default {
	props: ['workerId', 'formVisible'],
	data() {
		return {
			formData: {}
		}
	},
	watch: {
		workerId(val) {
			if (val) {
				this.getDetail(val)
			}
		}
	},
	methods: {
		getDetail(id) {
			getWorkmanDetail({ id }).then(res => {
				console.log(res)
				this.formData = res
			})
		},
		hideForm() {
			this.$emit('hideForm')
		}
	}
}
</script>
