<template>
  <div>
    <el-form :inline="true" :model="query" class="query-form">
      <el-form-item class="query-form-item">
        <el-input v-model="query.username" placeholder=""></el-input>
      </el-form-item>
      <el-form-item class="query-form-item">
        <el-select v-model="query.status" placeholder="">
          <el-option label="" value=""></el-option>
          <el-option label="" value="0"></el-option>
          <el-option label="" value="1"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item class="query-form-item">
        <el-select v-model="query.role_id" placeholder="">
          <el-option label="" value=""></el-option>
          <el-option v-for="item in roles" :key="item.id" :label="item.name" :value="item.id"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button-group>
          <el-button type="primary" icon="el-icon-refresh" @click="onReset"></el-button>
          <el-button type="primary" icon="search" @click="onSubmit"></el-button>
          <el-button type="primary" @click.native="handleForm(null,null)"></el-button>
        </el-button-group>
      </el-form-item>
    </el-form>
    <el-table
      v-loading="loading"
      class="common-table"
      :data="list"
      style="width: 100%;"
      max-height="500"
    >
      <el-table-column label=" ID" align="center" prop="id" fixed></el-table-column>
      <el-table-column label="" prop="username" align="center" fixed></el-table-column>
      <el-table-column label="" align="center">
        <template slot-scope="scope">
          <el-tag
            :type="scope.row.status | statusFilterType"
          >{{scope.row.status | statusFilterName}}</el-tag>
        </template>
      </el-table-column>
      <el-table-column label="" with="300" align="center" :show-overflow-tooltip="true">
        <template slot-scope="scope">
          <span>{{ scope.row.last_login_time }}</span>
        </template>
      </el-table-column>
      <el-table-column label="" align="center" fixed="right">
        <template slot-scope="scope">
          <el-button type="text" size="small" @click.native="handleForm(scope.$index, scope.row)"></el-button>
          <el-button type="text" size="small" @click.native="handleDel(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      :page-size="query.limit"
      @current-change="handleCurrentChange"
      layout="prev, pager, next"
      :total="total"
    ></el-pagination>
    <!---->
    <el-dialog
      :title="formMap[formName]"
      :visible.sync="formVisible"
      :before-close="hideForm"
      width="85%"
      top="5vh"
    >
      <el-form :model="formData" :rules="formRules" ref="dataForm">
        <el-form-item label="" prop="username">
          <el-input v-model="formData.username" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input type="password" v-model="formData.password" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="checkPassword">
          <el-input type="password" v-model="formData.checkPassword" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="status">
          <el-radio-group v-model="formData.status">
            <el-radio :label="0"></el-radio>
            <el-radio :label="1"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" required>
          <el-checkbox-group v-model="rolesList">
            <el-checkbox v-for="item in roles" :key="item.id" :label="item.id">{{item.name}}</el-checkbox>
          </el-checkbox-group>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="hideForm"></el-button>
        <el-button type="primary" @click.native="formSubmit()" :loading="formLoading"></el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {
  authAdminList,
  authAdminRoleList,
  authAdminSave,
  authAdminDelete
} from "../../../api/auth/authAdmin";
const formJson = {
  password: "",
  username: "",
  status: 0,
  roles: ''
};
export default {
  data() {
    let validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    let validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else if (value !== this.formData.password) {
        callback(new Error("!"));
      } else {
        callback();
      }
    };
    return {
      roles: [],
      rolesList: [],
      query: {
        username: "",
        status: "",
        page: 1,
        limit: 20,
        role_id: ""
      },
      list: [],
      total: 0,
      loading: true,
      index: null,
      formName: null,
      formMap: {
        add: "",
        edit: ""
      },
      formLoading: false,
      formVisible: false,
      formData: formJson,
      formRules: {},
      addRules: {
        username: [
          { required: true, message: "", trigger: "blur" }
        ],
        password: [
          { required: true, message: "", trigger: "blur" },
          { validator: validatePass, trigger: "blur" }
        ],
        checkPassword: [
          {
            required: true,
            message: "",
            trigger: "blur"
          },
          { validator: validatePass2, trigger: "blur" }
        ],
        status: [
          { required: true, message: "", trigger: "change" }
        ]
      },
      editRules: {
        username: [
          { required: true, message: "", trigger: "blur" }
        ],
        status: [
          { required: true, message: "", trigger: "change" }
        ]
      },
      deleteLoading: false
    };
  },
  methods: {
    onReset() {
      this.$router.push({
        path: ""
      });
      this.query = {
        username: "",
        status: "",
        page: 1,
        limit: 20,
        role_id: ""
      };
      this.getList();
    },
    onSubmit() {
      this.$router.push({
        path: "",
        query: this.query
      });
      this.getList();
    },
    handleCurrentChange(val) {
      this.query.page = val;
      this.getList();
    },
    getList() {
      this.loading = true;
      authAdminList(this.query)
        .then(response => {
          this.loading = false
          this.list = response.admin_list.data || [];
          this.total = response.admin_list.total
        })
        .catch(() => {
          this.loading = false;
          this.total = 0
          this.list = [];
          this.roles = [];
        });
    },
    getRoleList() {
      authAdminRoleList(this.query)
        .then(response => {
          this.roles = response || [];
        })
        .catch(() => {
          this.roles = [];
        });
    },
    // 
    resetForm() {
      if (this.$refs["dataForm"]) {
        // 
        this.$refs["dataForm"].clearValidate();
        // 
        this.$refs["dataForm"].resetFields();
      }
    },
    // 
    hideForm() {
      // 
      this.formVisible = !this.formVisible;
      this.formLoading = false;
      this.deleteLoading = false;
      // 
      this.$refs["dataForm"].resetFields();
      return true;
    },
    // 
    handleForm(index, row) {
      this.formVisible = true;
      this.formData = JSON.parse(JSON.stringify(formJson));
      if (row !== null) {
        this.formData = Object.assign({}, row);
        console.log(row)
        if (row.roles) {
          // this.rolesList = row.roles.split(',')
        }
      }
      this.formName = "add";
      this.formRules = this.addRules;
      if (index !== null) {
        this.index = index;
        this.formName = "edit";
        this.formRules = this.editRules;
      }
    },
    formSubmit() {
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
          this.formLoading = true;
          if (!this.rolesList.length) {
            return this.$message.warning('')
          }
          if (this.rolesList.length) {
            this.formData.roles = this.rolesList.join(',')
          }
          delete this.formData.checkPassword
          let data = Object.assign({}, this.formData);
          authAdminSave(data, this.formName).then(response => {
            if (response) {
              this.formLoading = false;
              this.$message.success("");
              this.formVisible = false;
              this.getList()
              // 
              this.resetForm();
            } else {
              this.$message.error("");
            }
          });
        }
      });
    },
    // 
    handleDel(index, row) {
      if (row.id) {
        this.$confirm("?", "", {
          type: "warning"
        })
          .then(() => {
            let para = { id: row.id };
            this.deleteLoading = true;
            authAdminDelete(para)
              .then(response => {
                this.deleteLoading = false;
                if (response.data) {
                  this.getList()
                  this.$message.success("")
                } else {
                  this.$message.error("")
                }
              })
              .catch(() => {
                this.deleteLoading = false;
              });
          })
          .catch(() => {
            this.$message.info("");
          });
      }
    }
  },
  filters: {
    statusFilterType(status) {
      const statusMap = {
        0: "success",
        1: "danger"
      };
      return statusMap[status];
    },
    statusFilterName(status) {
      const statusMap = {
        1: "",
        0: ""
      };
      return statusMap[status];
    }
  },
  mounted() { },
  created() {
    // 
    let query = this.$route.query;
    this.query = Object.assign(this.query, query);
    this.query.limit = parseInt(this.query.limit);
    // 
    this.getList();
    // 
    this.getRoleList();
  }
};
</script>