<template>
	<el-dialog
		title=""
		:visible.sync="formVisible"
		:before-close="hideForm"
		width="40%"
		top="5vh"
		class="form-detail-dialog detail-view"
	>
		<el-form
			:model="formData"
			:inline="true"
			label-width="160px"
			label-position="right"
			class="form"
			ref="dataForm"
			v-if="formData"
		>
			<el-form-item label="">
				<el-input v-model="formData.com_name" class="width240" readonly></el-input>
			</el-form-item>
			<el-form-item label="">
				<el-input class="width240" v-model="formData.com_type_name" readonly></el-input>
			</el-form-item>
			<el-form-item label="" >
				<el-input v-model="formData.permit_id"  class="width240" readonly></el-input>
			</el-form-item>
			<el-form-item label="" >
				<el-input class="width240" v-model="formData.corporation_name" readonly></el-input>
			</el-form-item>
			<el-form-item label="" >
				<el-input class="width240" v-model="formData.corporation_tel" readonly></el-input>
			</el-form-item>
			<el-form-item label="" >
				<el-input
					class="width240"
					v-model="formData.corporation_idcard"
					readonly
				></el-input>
			</el-form-item>
		</el-form>
	</el-dialog>
</template>
<script>
import {
	getDetail
} from "../../api/company/index";
export default {
	props: ['companyId', 'formVisible'],
	data() {
		return {
			formData: {}
		}
	},
	watch: {
		companyId(val) {
			if (val) {
				this.getCompanyDetail(val)
			}
		}
	},
	methods: {
		getCompanyDetail(id) {
			getDetail({ id }).then(res => {
				console.log(res)
				this.formData = res
			})
		},
		hideForm() {
			this.$emit('hideForm')
		}
	}
}
</script>
<style lang="scss">
.form-detail-dialog {
  .form {
  }
}
</style>


